import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import normalizeUrl from 'normalize-url';

const ProfileTop = ({
  profile: {
    status,
    company,
    website,
    location,
    social,
    user: { name, avatar },
  },
}) => {
  console.log(normalizeUrl(avatar, { forceHttps: true }));
  return (
    <div className='profile-top bg-primary p-1'>
      <img
        className='round-img my-1'
        src={normalizeUrl(avatar, { forceHttps: true })}
        alt=''
      />
      <h1 className='large'>{name}</h1>
      <p className='lead'>
        {status} {company && <span>at {company}</span>}
      </p>
      <p className='loca'>{location && <span>{location}</span>}</p>
      <div className='icons my-1'>
        {website != null && (
          <a href={website} target='_blank' rel='noopener noreferrer'>
            <i className='fas fa-globe fa-2x'></i>
          </a>
        )}

        {social && social.twitter && (
          <a href={social.twitter} target='_blank' rel='noopener noreferrer'>
            <i className='fab fa-twitter fa-2x'></i>
          </a>
        )}
        {social && social.facebook && (
          <a href={social.facebook} target='_blank' rel='noopener noreferrer'>
            <i className='fab fa-facebook fa-2x'></i>
          </a>
        )}
        {social && social.linkedin && (
          <a href={social.linkedin} target='_blank' rel='noopener noreferrer'>
            <i className='fab fa-linkedin fa-2x'></i>
          </a>
        )}
        {social && social.youtube && (
          <a href={social.youtube} target='_blank' rel='noopener noreferrer'>
            <i className='fab fa-youtube fa-2x'></i>
          </a>
        )}
        {social && social.instagram && (
          <a href={social.instagram} target='_blank' rel='noopener noreferrer'>
            <i className='fab fa-instagram fa-2x'></i>
          </a>
        )}
      </div>
    </div>
  );
};

ProfileTop.propTypes = {
  profile: PropTypes.object.isRequired,
};

export default connect(null)(ProfileTop);
